<template>
  <div>
    <h1>돈 나누기 계산기</h1>
    <div>
      <label for="totalAmount">총 금액:</label>
      <input
        id="totalAmount"
        type="number"
        v-model.number="totalAmount"
        placeholder="총 금액을 입력해주세요"
      />
    </div>
    <div>
      <label for="numberOfPeople">인원 수:</label>
      <input
        id="numberOfPeople"
        type="number"
        v-model.number="numberOfPeople"
        @change="updatePeople"
        placeholder="총 인원 수를 입력해주세요"
      />
    </div>
    <div v-if="numberOfPeople > 0">
      <h2>각 인원 정보</h2>
      <div v-for="(person, index) in people" :key="index">
        <label :for="'name' + index">이름:</label>
        <input
          :id="'name' + index"
          type="text"
          v-model="person.name"
          placeholder="이름을 입력해주세요"
        />
        <label :for="'amount' + index">개인 금액:</label>
        <input
          :id="'amount' + index"
          type="number"
          v-model.number="person.amount"
          placeholder="개인 금액을 입력해주세요"
          min="0"
        />
      </div>
    </div>
    <div>
      <button @click="calculateAmounts">계산하기</button>
    </div>
    <div v-if="calculated">
      <h2>계산 결과</h2>
      <p>공통으로 낼 금액: {{ commonAmount.toFixed(2) }} 원</p>
      <ul>
        <li v-for="(person, index) in people" :key="index">
          {{ person.name }}: {{ person.amount.toFixed(2) }} 개인금액
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MoneySplitter', 
  data() {
    return {
      totalAmount: 0,
      numberOfPeople: 1,
      people: [],
      calculated: false,
      commonAmount: 0,
    };
  },
  methods: {
    updatePeople() {
      this.people = Array.from({ length: this.numberOfPeople }, (_, index) => ({
        name: '',
        amount: 0,
        id: index,
      }));
    },
    calculateAmounts() {
      const totalPersonalAmount = this.people.reduce(
        (sum, person) => sum + parseFloat(person.amount || 0),
        0
      );

      if (this.numberOfPeople > 0) {
        this.commonAmount = (this.totalAmount - totalPersonalAmount) / this.numberOfPeople;
      } else {
        this.commonAmount = 0;
      }
      this.calculated = true;
    },
  },
};
</script>

<style scoped>
div {
  margin-bottom: 10px;
}

input {
  margin-left: 10px;
  padding: 5px;
  width: 200px;
}

h1, h2 {
  margin-bottom: 20px;
}

h2 {
  color: #007bff;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin: 5px 0;
}
</style>